import React, { FC } from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';
import { Message } from '../reducers/kid';

const companies = ['facebook', 'airbnb', 'netflix'];

export interface MessageBoxProps {
    message: Message;
}

const MessageBox: FC<MessageBoxProps> = ({message}) => {
    const [open, setOpen] = React.useState(false);

    React.useEffect(()=> {
        message ? setOpen(true) : setOpen(false)
    }, []);

    function handleClickOpen() {
        setOpen(true);
    }


    function handleClose() {
        setOpen(false);
    }

    return (
        <div>
            {/* <Button variant="outlined" color="primary" onClick={handleClickOpen}>
                Open alert dialog
            </Button> */}
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
                fullWidth={true}
            >
                <DialogTitle id="alert-dialog-title">{message.title}</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        {message.content}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} color="primary" autoFocus>
                        Đóng
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
};

export default MessageBox;
