import Drawer from '@material-ui/core/Drawer';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { makeStyles } from '@material-ui/core/styles';
import Favorite from '@material-ui/icons/Favorite';
import MenuIcon from '@material-ui/icons/Menu';
import React, { FC } from 'react';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles({
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
});

interface AppDrawerProps {
  classList?: string[];
}

const AppDrawer: FC<AppDrawerProps> = ({
  classList = []
}) => {
  console.log("AppDrawer");
  console.log(classList);
  const classes = useStyles();
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  type DrawerSide = 'top' | 'left' | 'bottom' | 'right';
  const toggleDrawer = (side: DrawerSide, open: boolean) => (
    event: React.MouseEvent,
  ) => {
    setState({ ...state, [side]: open });
  };

  const sideList = (side: DrawerSide) => (
    <div
      className={classes.list}
      role="presentation"
      onClick={toggleDrawer(side, false)}
    >
      <List>
        {classList.map((item, index) => (
          <>
            <ListItem button key={index} component="a" href={`/#/${item}`}
              onClick={() => setTimeout(() => location.reload(), 1000)}
              style={{ backgroundColor: item, borderStyle: "solid", borderColor: "white", borderWidth: 2, borderRadius: 5 }}>
              <ListItemIcon>
                <Favorite color="secondary" />
              </ListItemIcon>
              <ListItemText disableTypography
                primary={<Typography style={{ color: '#FFFFFF' }}>
                  {item.toUpperCase()}
                </Typography>} />
            </ListItem>
            <Divider />
          </>
        ))}
      </List>
    </div>
  );

  return (
    <div>
      <IconButton
        edge="start"
        // className={classes.menuButton}
        color="inherit"
        aria-label="open drawer"
        onClick={toggleDrawer('left', true)}
      >
        <MenuIcon />
      </IconButton>
      <Drawer open={state.left} onClose={toggleDrawer('left', false)}>
        {sideList('left')}
      </Drawer>
    </div>
  );
}

export default AppDrawer;