export const selectKid = (id: string) => ({
  type: ActionType.SELECT_KID as typeof ActionType.SELECT_KID,
  payload: id,
});

export const selectAll = (kidList: Kid[]) => ({
  type: ActionType.SELECT_ALL as typeof ActionType.SELECT_ALL,
  payload: kidList,
});

export const unselectAll = (kidList: Kid[]) => ({
  type: ActionType.UNSELECT_ALL as typeof ActionType.UNSELECT_ALL,
  payload: kidList,
});

export const search = (keyword: string) => ({
  type: ActionType.SEARCH as typeof ActionType.SEARCH,
  payload: keyword,
});

export const filterKid = (filterVal: string) => ({
  type: ActionType.FILTER_KID as typeof ActionType.FILTER_KID,
  payload: filterVal,
});

export const sendMessagesTest = (kidList: Kid[]) => ({
  type: ActionType.SEND_MESSAGES as typeof ActionType.SEND_MESSAGES,
  payload: kidList,
});

export const sendMessages = {
  start: (kidList: Kid[], messageType: string) => ({
    type: ActionType.SEND_MESSAGES_START as typeof ActionType.SEND_MESSAGES_START,
    payload: { kidList, messageType },
  }),

  success: (kid: Kid) => ({
    type: ActionType.SEND_MESSAGES_SUCCESS as typeof ActionType.SEND_MESSAGES_SUCCESS,
    payload: kid,
  }),

  failure: (error: AxiosError) => ({
    type: ActionType.SEND_MESSAGES_FAILURE as typeof ActionType.SEND_MESSAGES_FAILURE,
    payload: error,
  }),
};

export const updateAirTable = {
  start: (kidList: Kid[]) => ({
    type: ActionType.UPDATE_AIRTABLE_START as typeof ActionType.UPDATE_AIRTABLE_START,
    payload: kidList
  }),

  success: (kid: Kid) => ({
    type: ActionType.UPDATE_AIRTABLE_SUCCESS as typeof ActionType.UPDATE_AIRTABLE_SUCCESS,
    payload: kid,
  }),

  failure: (error: AxiosError) => ({
    type: ActionType.UPDATE_AIRTABLE_FAILURE as typeof ActionType.UPDATE_AIRTABLE_FAILURE,
    payload: error,
  }),
};

export const getKidList = {
  start: (params: string) => ({
    type: ActionType.GETKIDLIST_START as typeof ActionType.GETKIDLIST_START,
    payload: params,
  }),

  success: (result: Kid[]) => ({
    type: ActionType.GETKIDLIST_SUCCESS as typeof ActionType.GETKIDLIST_SUCCESS,
    payload: result,
  }),

  failure: (params: AxiosError) => ({
    type: ActionType.GETKIDLIST_FAILURE as typeof ActionType.GETKIDLIST_FAILURE,
    payload: params,
  }),
};

export const getClassList = {
  start: () => ({
    type: ActionType.GETCLASSLIST_START as typeof ActionType.GETCLASSLIST_START,
  }),

  success: (classes: string[]) => ({
    type: ActionType.GETCLASSLIST_SUCCESS as typeof ActionType.GETCLASSLIST_SUCCESS,
    payload: classes,
  }),

  failure: (params: AxiosError) => ({
    type: ActionType.GETCLASSLIST_FAILURE as typeof ActionType.GETCLASSLIST_FAILURE,
    payload: params,
  }),
};

export type Action =
  | ReturnType<typeof sendMessagesTest>
  | ReturnType<typeof selectKid>
  | ReturnType<typeof selectAll>
  | ReturnType<typeof unselectAll>
  | ReturnType<typeof filterKid>
  | ReturnType<typeof search>
  | ReturnType<typeof updateAirTable.start>
  | ReturnType<typeof updateAirTable.success>
  | ReturnType<typeof updateAirTable.failure>
  | ReturnType<typeof sendMessages.start>
  | ReturnType<typeof sendMessages.success>
  | ReturnType<typeof sendMessages.failure>
  | ReturnType<typeof getKidList.start>
  | ReturnType<typeof getKidList.success>
  | ReturnType<typeof getKidList.failure>
  | ReturnType<typeof getClassList.start>
  | ReturnType<typeof getClassList.success>
  | ReturnType<typeof getClassList.failure>;
