import React, { FC, useEffect } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { RouteComponentProps, withRouter } from 'react-router';
import { Kid } from '../services/models';
import { AppState } from '../reducers/kid';
import { getKidList, selectKid, sendMessages, filterKid, selectAll, unselectAll, search } from '../actions/kid';
import Kids, { KidsProps } from '../components/Kids';

interface StateProps {
  kidList: Kid[];
  isLoading: boolean;
  filterVal?: string;
  searchVal?: string;
  classList?: string[];
}

interface DispatchProps {
  getKidListStart: (className: string) => void;
  selectKid: (id: string) => void;
  sendMessages: (kidList: Kid[], messageType: string) => void;
  filterKid: (filterVal: string) => void;
  selectAll: (kidList: Kid[]) => void;
  unselectAll: (kidList: Kid[]) => void;
  search: (keyword: string) => void;
}

type EnhancedKidsProps = KidsProps & 
  StateProps &
  DispatchProps &
  RouteComponentProps<{ kidClass: string }>;

const mapStateToProps = (state: AppState): StateProps => ({
  kidList: state.kids,
  filterVal: state.filterVal,
  searchVal: state.searchVal,
  isLoading: state.isLoading,
  classList: state.classList,
});

const mapDispatchToProps = (dispatch: Dispatch): DispatchProps =>
  bindActionCreators(
    {
      getKidListStart: (kidClass: string) =>
        getKidList.start(kidClass),

      selectKid: (id: string) =>
        selectKid(id),

      sendMessages: (kidList: Kid[], messageType: string) =>
        sendMessages.start(kidList, messageType),

      filterKid: (filterVal: string) =>
        filterKid(filterVal),

      selectAll: (kidList: Kid[]) =>
        selectAll(kidList),

      unselectAll: (kidList: Kid[]) =>
        unselectAll(kidList),

      search: (keyword: string) =>
        search(keyword),
    },
    dispatch,
  );

const KidsContainer: FC<EnhancedKidsProps> = ({
  kidList,
  isLoading,
  getKidListStart,
  selectKid,
  sendMessages,
  filterKid,
  filterVal,
  selectAll,
  unselectAll,
  search,
  searchVal,
  match,
}) => {
  const { kidClass } = match.params;

  useEffect(() => {
    localStorage.setItem("class", kidClass);
    console.log("KidsContainer " + kidClass);
    setTimeout(() => getKidListStart(kidClass), 1000);
    // getMembersStart(kidClass);
    // sendMessages(kidList, "");
  }, []);

  return (
    <Kids kidList={kidList} 
      isLoading={isLoading} 
      getKidListStart={getKidListStart} 
      selectKid={selectKid}
      filterKid={filterKid}
      sendMessages={sendMessages}
      filterVal={filterVal}
      selectAll={selectAll}
      unselectAll={unselectAll}
      search={search}
      searchVal={searchVal} />
  );
};

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(KidsContainer),
);
