import { Reducer } from 'redux';
import { AxiosError } from 'axios';

import { Action } from '../actions/kid';
import * as ActionType from '../actions/actionConstants';
import { Kid } from "../services/models";
import * as AppConstants from '../appConstants';

export interface Message {
  title: string;
  content: string;
}

export interface AppState {
  kids: Kid[];
  classList: string[];
  filterVal?: string;
  searchVal?: string;
  isLoading: boolean;
  error?: AxiosError;
  message?: Message;
}

export const initialState: AppState = {
  kids: [],
  classList: [],
  filterVal: AppConstants.FILTER_ALL,
  isLoading: false,
};

const kidReducer: Reducer<AppState, Action> = (
  state: AppState = initialState,
  action: Action,
): AppState => {
  switch (action.type) {
    case ActionType.GETKIDLIST_START:
      return {
        ...state,
        kids: [],
        isLoading: true,
      };
    case ActionType.GETKIDLIST_SUCCESS:
      return {
        ...state,
        kids: [...action.payload],
        isLoading: false,
      };
    case ActionType.GETKIDLIST_FAILURE:
      return {
        ...state,
        isLoading: false,
        // message: {
        //   title: "LỖI",
        //   content: "Đã có lỗi xảy ra"
        // },
        error: action.payload,
      };

    case ActionType.GETCLASSLIST_START:
      return {
        ...state,
        isLoading: true,
      };
    case ActionType.GETCLASSLIST_SUCCESS:
      return {
        ...state,
        classList: [...action.payload],
        isLoading: false,
      };
    case ActionType.GETCLASSLIST_FAILURE:
      return {
        ...state,
        isLoading: false,
        // message: {
        //   title: "LỖI",
        //   content: "Đã có lỗi xảy ra"
        // },
        error: action.payload,
      };

    case ActionType.SEND_MESSAGES_START:
      return {
        ...state,
        isLoading: true,
      };
    case ActionType.SEND_MESSAGES_SUCCESS:
      return {
        ...state,
        // message: {
        //   title: "THÀNH CÔNG",
        //   content: "Đã gửi thành công"
        // },
        isLoading: false,
      };
    case ActionType.SEND_MESSAGES_FAILURE:
      return {
        ...state,
        isLoading: false,
        // message: {
        //   title: "LỖI",
        //   content: "Đã có lỗi xảy ra khi gửi"
        // },
        error: action.payload,
      };

    case ActionType.UPDATE_AIRTABLE_START:
      return {
        ...state,
        isLoading: true,
      };
    case ActionType.UPDATE_AIRTABLE_SUCCESS:
      return {
        ...state,
        kids: state.kids.map(item => {
          let kidList = state.kids;
          for (let i = 0; i < kidList.length; i++) {
            if (kidList[i].isSelected) {
              item.isSelected = false;
            }
          }

          return item;
        }),
        // kids: state.kids.map(item => {
        //   if (action.payload.id === item.id) {
        //     item.status = action.payload.status;
        //     item.isSelected = false;
        //   }
        //   return item;
        // }),
        // filterKids: [...state.kids],
        isLoading: false,
      };
    case ActionType.UPDATE_AIRTABLE_FAILURE:
      return {
        ...state,
        isLoading: false,
        // message: {
        //   title: "LỖI",
        //   content: "Đã có lỗi xảy ra"
        // },
        error: action.payload,
      };

    case ActionType.SELECT_KID:
      return {
        ...state,
        kids: state.kids.map(item => {
          if (item.id === action.payload) {
            item.isSelected = !item.isSelected;
          }
          return item;
        })
      };

    case ActionType.SELECT_ALL:
      return {
        ...state,
        kids: state.kids.map(item => {
          let kidList = action.payload;
          for (let i = 0; i < kidList.length; i++) {
            if (item.id === kidList[i].id) {
              item.isSelected = true;
            }
          }

          return item;
        })
      };

    case ActionType.UNSELECT_ALL:
      return {
        ...state,
        kids: state.kids.map(item => {
          let kidList = action.payload;
          for (let i = 0; i < kidList.length; i++) {
            if (item.id === kidList[i].id) {
              item.isSelected = false;
            }
          }

          return item;
        })
      };


    case ActionType.SEARCH:
      return {
        ...state,
        searchVal: action.payload
      };

    case ActionType.FILTER_KID:
      return {
        ...state,
        filterVal: action.payload,
        kids: state.kids.map(item => {
          if (item.isSelected) {
            item.isSelected = false
          }

          return item;
        })
        // filterKids: state.kids.filter(item => {
        //   item.isSelected = false;

        //   if (action.payload === AppConstants.FILTER_ALL) {
        //     return true;
        //   }

        //   if (item.status) {
        //     switch (action.payload) {
        //       case AppConstants.FILTER_GOSCHOOL:
        //         if (item.status === AppConstants.STATUS_GO_SCHOOL ||
        //           item.status === AppConstants.STATUS_LEAVE_SCHOOL) {
        //           return true;
        //         }
        //       case AppConstants.FILTER_ABSENT:
        //         if (item.status === AppConstants.STATUS_ABSENT ||
        //           item.status === AppConstants.STATUS_ABSENT_ALLOW) {
        //           return true;
        //         }
        //       default:
        //         return false;
        //     }
        //   }

        //   if (action.payload === AppConstants.FILTER_UNKNOWN) {
        //     return true;
        //   }
        //   else {
        //     return false;
        //   }
        // })
      };

    default: {
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      // const _: never = action;

      return state;
    }
  }
};

export default kidReducer;
