import React, { FC, useEffect } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { getClassList } from '../actions/kid';
import AppDrawer from '../components/AppDrawer';
import { AppState } from '../reducers/kid';


interface StateProps {
    classList: string[];
}

interface DispatchProps {
    getClassListStart: () => void;
}

type EnhancedProps = StateProps &
    DispatchProps;

const mapStateToProps = (state: AppState): StateProps => ({
    classList: state.classList
});

const mapDispatchToProps = (dispatch: Dispatch): DispatchProps =>
    bindActionCreators(
        {
            getClassListStart: () =>
                getClassList.start(),
        },
        dispatch,
    );

const AppDrawerContainer: FC<EnhancedProps> = ({
    classList,
    getClassListStart
}) => {

    useEffect(() => {
        setTimeout(() => getClassListStart(), 1000);
    }, []);

    return (
        <AppDrawer classList={classList} />
    );
};

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(AppDrawerContainer);
