import React, { FC } from 'react';
import FacebookLogin from 'react-facebook-login';
// import FacebookLogin from 'react-facebook-login/dist/facebook-login-render-props';
import { Redirect } from 'react-router-dom';
import { LoginUser } from '../services/models';

const responseSuccess = (response: any) => {
  console.log(response);
  console.log(response.accessToken);

  let postData;
  if (response.w3.U3) {
    postData = {
      access_token: response.Zi.access_token,
    };
  }

  if (postData) {
    console.log(postData);
    console.log(JSON.stringify(postData));
    localStorage.setItem("loginUser", JSON.stringify(postData));
    console.log("signup" + localStorage.getItem("loginUser"));
  }
}

const responseFailure = (response: any) => {
  console.log(response);
}

const responseFacebook = (response: any) => {
  console.log("responseFacebook");
  if (response.name) {
    let loginUser: LoginUser = {
      name: response.name,
      email: response.email,
      picture: response.picture.data.url
    };
    localStorage.setItem("loginUser", JSON.stringify(loginUser));
    location.reload();
  }
  console.log(response);
};

const Login: FC = () => {
  try {

    let localStore = localStorage.getItem('loginUser');
    if (localStore) {
      let loginUser: LoginUser = JSON.parse(localStore);
      if (loginUser.name) {
        return (<Redirect to={'/Red'} />)
      }

    }
  } catch (err) {
    console.log("Login");
    console.log(err);
  }


  return (
    <>
      {/* <div className="fb-login-button" data-width="" data-size="large" data-button-type="login_with" data-auto-logout-link="false" data-use-continue-as="false"> */}


      <FacebookLogin
        appId="2598444130186911"
        autoLoad={true}
        fields="name,email,picture"
        callback={responseFacebook}
        // cssClass="my-facebook-button-class"
        // icon="fa-facebook"
        redirectUri="nhanqv.github.io"
      />
    </>

  );
}

export default Login;