import { all, call, fork, put, takeLatest } from 'redux-saga/effects';
import * as ActionType from '../actions/actionConstants';
import { getKidList, sendMessagesTest, sendMessages, updateAirTable, getClassList } from '../actions/kid';
import { getKidListFactory, sendMessagesFactory, updateAirTableFactory, getClassListFactory } from '../services/api';
import { Kid } from '../services/models';

function* runGetKidList(action: ReturnType<typeof getKidList.start>) {
  console.log("runGetKidList" + action.payload);
  const kidClass = action.payload;

  try {
    const api = getKidListFactory();
    const result = yield call(api, kidClass);

    yield put(getKidList.success(result));
  } catch (error) {
    yield put(getKidList.failure(error));
  }
}

export function* watchGetKidList() {
  console.log("watchGetKidList");
  yield takeLatest(ActionType.GETKIDLIST_START, runGetKidList);
}

function* runGetClassList(action: ReturnType<typeof getClassList.start>) {

  try {
    const api = getClassListFactory();
    const result = yield call(api);

    yield put(getClassList.success(result));
  } catch (error) {
    yield put(getClassList.failure(error));
  }
}

export function* watchGetClassList() {
  console.log("watchGetClassList");
  yield takeLatest(ActionType.GETCLASSLIST_START, runGetClassList);
}

function* runSendMessages(action: ReturnType<typeof sendMessages.start>) {
  console.log("runSendMessages");
  const kidList: Kid[] = action.payload.kidList;
  const messageType: string = action.payload.messageType;

  for (let i = 0; i < kidList.length; i++) {
    if (kidList[i].isSelected) {
      try {
        const api = sendMessagesFactory();
        const result = yield call(api, kidList[i], messageType);

        if (result) {
          kidList[i].status = messageType;
          yield put(sendMessages.success(kidList[i]));
        }

      } catch (error) {
        console.log(error);
        yield put(sendMessages.failure(error));
      }
    }
  }

  yield put(updateAirTable.start(kidList));

}

export function* watchSendMessages() {
  console.log("watchSendMessages");
  yield takeLatest(ActionType.SEND_MESSAGES_START, runSendMessages);
}

function* runUpdateAirTable(action: ReturnType<typeof updateAirTable.start>) {
  console.log("runUpdateAirTable");
  const kidList = action.payload;

  try {
    const api = updateAirTableFactory();
    const result = yield call(api, kidList);

    yield put(updateAirTable.success(result));
  } catch (error) {
    yield put(updateAirTable.failure(error));
  }
}

export function* watchUpdateAirTable() {
  console.log("watchUpdateAirTable");
  yield takeLatest(ActionType.UPDATE_AIRTABLE_START, runUpdateAirTable);
}

export default function* rootSaga() {
  console.log("rootSaga");
  yield all([fork(watchGetKidList), 
            fork(watchGetClassList), 
            fork(watchSendMessages), 
            fork(watchUpdateAirTable)]);
}
