import AppBar from "@material-ui/core/AppBar";
import Container from '@material-ui/core/Container';
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import GridListTileBar from "@material-ui/core/GridListTileBar";
import IconButton from "@material-ui/core/IconButton";
import InputBase from '@material-ui/core/InputBase';
import InputLabel from '@material-ui/core/InputLabel';
import LinearProgress from '@material-ui/core/LinearProgress';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import { createStyles, fade, makeStyles, Theme, useTheme } from "@material-ui/core/styles";
import Toolbar from "@material-ui/core/Toolbar";
import useMediaQuery from '@material-ui/core/useMediaQuery';
import CheckCircle from "@material-ui/icons/CheckCircle";
import FiberManualRecord from '@material-ui/icons/FiberManualRecord';
import MoreIcon from '@material-ui/icons/MoreVert';
import SearchIcon from '@material-ui/icons/Search';
import * as React from "react";
import { FC } from "react";
import * as MessageType from '../actions/messageConstants';
import * as AppConstants from '../appConstants';
import { Kid } from "../services/models";
import Box from '@material-ui/core/Box';
import MessageBox from '../containers/MessageBox';
import { Message } from '../reducers/kid';
import AppDrawer from '../containers/AppDrawer';
import { LoginUser } from '../services/models';
import { Redirect } from 'react-router-dom';

export interface KidsProps {
  left?: boolean;
  kidList?: Kid[];
  selectKid?: (id: string) => void;
  isLoading: boolean;
  getKidListStart: (sheetName: string) => void;
  sendMessages?: (kidList: Kid[], messageType: string) => void;
  filterKid?: (filterVal: string) => void;
  filterVal?: string;
  selectAll?: (selectAll: Kid[]) => void;
  unselectAll?: (selectAll: Kid[]) => void;
  search?: (keyword: string) => void;
  searchVal?: string;
  message?: Message;
  classList?: string[];
}

const Kids: FC<KidsProps> = ({
  left = false,
  kidList = [],
  selectKid = () => { },
  isLoading = false,
  getKidListStart = () => { },
  sendMessages = () => { },
  filterKid = () => { },
  filterVal = "",
  selectAll = () => { },
  unselectAll = () => { },
  search = () => { },
  searchVal,
  message,
  classList = [],
}) => {

  try {

    let localStore = localStorage.getItem('loginUser');
    if (!localStore) {
      return (<Redirect to={'/'} />)
    } else {
      let loginUser: LoginUser = JSON.parse(localStore);
      if (!loginUser.name) {
        return (<Redirect to={'/'} />)
      }
    }
  } catch (err) {
    console.log("Login");
    console.log(err);
  }

  const useStyles = makeStyles((theme: Theme) =>
    createStyles({
      root: {
        display: "flex",
        flexWrap: "wrap",
        justifyContent: "space-around",
        overflow: "hidden",
        // backgroundColor: theme.palette.background.paper
      },
      gridListTile: {
        // width: 177,
        // height: 180,
        justify: "center",
        alignItems: "center"
      },
      icon: {
        color: "rgba(255, 255, 255, 0.54)"
      },
      button: {
        margin: theme.spacing(1),
        width: 80
      },
      rightIcon: {
        marginLeft: theme.spacing(1)
      },
      title: {
        flexGrow: 1
      },
      menuButton: {
        marginRight: theme.spacing(2)
      },
      list: {
        width: 250
      },
      redIcon: {
        color: "red"
      },
      greenIcon: {
        color: "green"
      },
      yellowIcon: {
        color: "yellow"
      },
      greyIcon: {
        color: "grey"
      },
      marginIcon: {
        margin: 10
      },
      progress: {
        margin: theme.spacing(2),
      },
      fab: {
        margin: theme.spacing(1),
        // right: 20,
        // position: 'fixed'
      },
      extendedIcon: {
        marginRight: theme.spacing(1),
      },
      formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
      },
      selectEmpty: {
        marginTop: theme.spacing(2),
      },
      search: {
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.common.white, 0.15),
        '&:hover': {
          backgroundColor: fade(theme.palette.common.white, 0.25),
        },
        marginRight: theme.spacing(2),
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
          marginLeft: theme.spacing(3),
          width: 'auto',
        },
      },
      searchIcon: {
        width: theme.spacing(7),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
      },
      inputRoot: {
        color: 'inherit',
      },
      inputInput: {
        padding: theme.spacing(1, 1, 1, 7),
        fontSize: 16,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
          width: 200,
        },
      },
      toolbar: theme.mixins.toolbar,
      grow: {
        flexGrow: 1,
      },
      gridListTileSelected: {
        // width: 177,
        // height: 180,
        justify: "center",
        alignItems: "center",
        borderStyle: "solid",
        borderWidth: 10,
        borderColor: "#d4d4d5",
      }
    })
  );
  // if(!localStorage.getItem('userData')){
  //   return (<Redirect to={'/'}/>)
  // }

  console.log(kidList);

  const classes = useStyles();

  var tileWidthPerc = "50%";

  const getGridListCols = () => {
    const theme = useTheme();
    // console.log(props.width);
    var numTiles = 2;
    if (useMediaQuery(theme.breakpoints.up('xl'))) {
      numTiles = 6;
      tileWidthPerc = "17%";
    }

    if (useMediaQuery(theme.breakpoints.up('lg'))) {
      numTiles = 5;
      tileWidthPerc = "20%";
    }

    if (useMediaQuery(theme.breakpoints.up('md'))) {
      numTiles = 4;
      tileWidthPerc = "25%";
    }

    if (useMediaQuery(theme.breakpoints.up('sm'))) {
      numTiles = 3;
      tileWidthPerc = "33%";
    }

    return numTiles;
  };

  console.log(getGridListCols());

  // // const [state, setState] = React.useState({
  // //   left: false,
  // //   kids: kids
  // // });

  // // const handleClick = (id: number) => (
  // //   event: React.MouseEvent<HTMLLIElement, MouseEvent>
  // // ) => {
  // //   console.log(id);
  // //   let newTileState = state.kids.map(tile =>
  // //     tile.id === id ? { ...tile, isSelected: !tile.isSelected } : tile
  // //   );

  // //   setState({ ...state, kids: newTileState });
  // //   console.log(state);
  // // };

  // type DrawerSide = "left";
  // const toggleDrawer = (side: DrawerSide, open: boolean) => (
  //   event: React.MouseEvent
  // ) => {
  //   // setState({ ...state, [side]: open });
  //   console.log();
  // };

  // const sideList = (side: DrawerSide) => (
  //   <div
  //     className={classes.list}
  //     role="presentation"
  //     onClick={toggleDrawer(side, false)}
  //   >
  //     <List>
  //       {["RED", "BLUE", "YELLOW", "GREEN"].map((text, index) => (
  //         <ListItem button key={text}>
  //           <ListItemIcon>
  //             <Face className={classes.redIcon} />
  //           </ListItemIcon>
  //           <ListItemText primary={text} />
  //         </ListItem>
  //       ))}
  //     </List>
  //     <Divider />
  //     {/* <List>
  //       {["All mail", "Trash", "Spam"].map((text, index) => (
  //         <ListItem button key={text}>
  //           <ListItemIcon>
  //             {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
  //           </ListItemIcon>
  //           <ListItemText primary={text} />
  //         </ListItem>
  //       ))}
  //     </List> */}
  //   </div>
  // );

  const gridListTileStyle = {
    width: tileWidthPerc,
    height: 180
  };

  const filterKids = kidList.filter(item => {
    let searchFound = false;

    if (searchVal) {
      console.log("search " + searchVal);
      if (khongDau(item.name.toLowerCase()).includes(khongDau(searchVal.toLowerCase()))) {
        searchFound = true;
      }
    }
    else {
      searchFound = true;
    }

    let filterFound = false;
    if (filterVal === AppConstants.FILTER_ALL) {
      filterFound = true;
    } else if (filterVal === AppConstants.FILTER_GOSCHOOL) {
      if (item.status === AppConstants.STATUS_GO_SCHOOL ||
        item.status === AppConstants.STATUS_LEAVE_SCHOOL) {
        filterFound = true;
      }
    } else if (filterVal === AppConstants.FILTER_ABSENT) {
      if (item.status === AppConstants.STATUS_ABSENT ||
        item.status === AppConstants.STATUS_ABSENT_ALLOW) {
        filterFound = true;
      }
    } else if (filterVal === AppConstants.FILTER_UNKNOWN) {
      if (!item.status) {
        filterFound = true;
      }
    }

    return searchFound && filterFound;
  });

  // Disable functions if there is no kids selected
  const isDisabled: boolean = filterKids.filter(item => item.isSelected).length === 0;

  function khongDau(inputStr: string) {
    var str = inputStr;
    str = str.toLowerCase();
    str = str.replace(/à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ/g, "a");
    str = str.replace(/è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ/g, "e");
    str = str.replace(/ì|í|ị|ỉ|ĩ/g, "i");
    str = str.replace(/ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ/g, "o");
    str = str.replace(/ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ/g, "u");
    str = str.replace(/ỳ|ý|ỵ|ỷ|ỹ/g, "y");
    str = str.replace(/đ/g, "d");
    str = str.replace(/!|@|%|\^|\*|\(|\)|\+|\=|\<|\>|\?|\/|,|\.|\:|\;|\'|\"|\&|\#|\[|\]|~|\$|_|`|-|{|}|\||\\/g, " ");
    str = str.replace(/ + /g, " ");
    str = str.trim();
    return str;
  }

  function handleFilter(event: React.ChangeEvent<{ name?: string; value: unknown }>) {
    var filterVal: string = event.target.value as string;
    console.log(event.target.value);
    filterKid(filterVal);
  }

  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);

  function handleMenuClick(event: React.MouseEvent<HTMLButtonElement>) {
    setAnchorEl(event.currentTarget);
  }

  function handleMenuClose(messageType: string) {
    setAnchorEl(null);
    console.log(messageType);
    if (messageType) {
      sendMessages(kidList, messageType)
    }

  }

  function handleSelectAll() {
    setAnchorEl(null);
    selectAll(filterKids);
  }

  function handleUnselectAll() {
    setAnchorEl(null);
    unselectAll(filterKids);
  }

  function handleSearch(event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) {
    search(event.target.value);
  }

  return (
    <div className={classes.root} >

      <Container>
        <AppBar position="fixed" color="secondary" >
          <Toolbar>
            <AppDrawer />
            <div className={classes.search}>
              <div className={classes.searchIcon}>
                <SearchIcon />
              </div>
              <InputBase
                placeholder="Tìm kiếm…"
                onChange={handleSearch}
                classes={{
                  root: classes.inputRoot,
                  input: classes.inputInput,
                }}
                inputProps={{ 'aria-label': 'search' }}
              />
            </div>

            <div className={classes.grow} />

            <IconButton edge="end" color="inherit" onClick={handleMenuClick}>
              <MoreIcon />
            </IconButton>
            <Menu
              id="simple-menu"
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={() => handleMenuClose("")}
            >
              <MenuItem onClick={handleSelectAll}>Chọn tất cả</MenuItem>
              <MenuItem onClick={handleUnselectAll}>Bỏ chọn tất cả</MenuItem>
              <MenuItem disabled={isDisabled} onClick={() => handleMenuClose(MessageType.MESSAGE_GO_SCHOOL)}>Đi học</MenuItem>
              <MenuItem disabled={isDisabled} onClick={() => handleMenuClose(MessageType.MESSAGE_LEAVE_SCHOOL)}>Đã về</MenuItem>
              <MenuItem disabled={isDisabled} onClick={() => handleMenuClose(MessageType.MESSAGE_ABSENT_ALLOW)}>Nghỉ có phép</MenuItem>
              <MenuItem disabled={isDisabled} onClick={() => handleMenuClose(MessageType.MESSAGE_ABSENT)}>Nghỉ không phép</MenuItem>
            </Menu>

          </Toolbar>
        </AppBar>
      </Container>


      <Container className={classes.toolbar}></Container>

      <Container style={{ paddingLeft: 0, paddingRight: 0, marginTop: -5 }}>
        {
          isLoading ? <LinearProgress /> : <div style={{ height: 4 }}> </div>
        }

      </Container>

      <Container>
        <Box display="flex" alignItems="center">
          <Box flexGrow={1}>
            <Select
              value={filterVal}
              onChange={handleFilter}
              className={classes.formControl}
            >
              <MenuItem value={AppConstants.FILTER_ALL}>Tất cả</MenuItem>
              <MenuItem value={AppConstants.FILTER_GOSCHOOL}>Đi học</MenuItem>
              <MenuItem value={AppConstants.FILTER_ABSENT}>Nghỉ học</MenuItem>
              <MenuItem value={AppConstants.FILTER_UNKNOWN}>Chưa thông báo</MenuItem>
            </Select>
          </Box>
          <Box>
            <InputLabel>Số bé: {filterKids.length}</InputLabel>
          </Box>
        </Box>
      </Container>

      <Container>
        <GridList
          cols={getGridListCols()}
          style={{ marginTop: 16 }}
        >
          {filterKids.map(item => (
            <GridListTile
              className={item.isSelected ? classes.gridListTileSelected : classes.gridListTile}
              style={gridListTileStyle}
              key={item.id}
              onClick={() => selectKid(item.id)}

            >
              <img src={item.avatar} alt={item.name} />
              <GridListTileBar
                title={item.name}
                actionIcon={
                  item.isSelected ? (
                    <CheckCircle className={classes.greenIcon} />
                  ) : (item.status == AppConstants.STATUS_GO_SCHOOL ? <FiberManualRecord className={classes.greenIcon} /> :
                    (item.status == AppConstants.STATUS_LEAVE_SCHOOL ? <FiberManualRecord className={classes.greyIcon} /> :
                      (item.status == AppConstants.STATUS_ABSENT_ALLOW ? <FiberManualRecord className={classes.yellowIcon} /> :
                        (item.status == AppConstants.STATUS_ABSENT ? <FiberManualRecord className={classes.redIcon} /> : ""))))
                }
              />
            </GridListTile>
          ))}
        </GridList>
        <MessageBox />

      </Container>

      {/* <Fab color="secondary" aria-label="edit" className={classes.fab} style={fabStyle}>
        <SendIcon />
      </Fab> */}
    </div>
  );
};

// export default withWidth()(Kids);
export default Kids;

