import axios from 'axios';
import { Kid } from './models';
import * as MessageType from '../actions/messageConstants';

interface ApiConfig {
  baseURL: string;
  timeout: number;
}

const AIRTABLE_BASE_KEY = "app8JrpvvNCWIOQAN";
const AIRTABLE_API_KEY = "keyfPK5EI1cgILohx";
const AIRTABLE_API_CONFIG: ApiConfig = {
  baseURL: 'https://api.airtable.com/v0',
  timeout: 7000,
};

export const getKidListFactory = (optionConfig?: ApiConfig) => {
  const config = {
    ...AIRTABLE_API_CONFIG,
    ...optionConfig,
  };
  const instance = axios.create(config);

  const getKidList = async (kidClass: string) => {
    try {
      console.log("getKidList");
      const requestUrl = `/${AIRTABLE_BASE_KEY}/${kidClass}?api_key=${AIRTABLE_API_KEY}&view=Kids`;
      const response = await instance.get(requestUrl);

      if (response.status !== 200) {
        throw new Error('Server Error');
      }

      let currentDay = new Date().getDate();

      console.log(response.data);
      const kidList: Kid[] = response.data.records.map((item: any) => {
        const val = item.fields;
        var kid: Kid = {
          id: val.Id,
          name: val.Name,
          contactIds: val.hasOwnProperty("ContactIds") ? val.ContactIds.split(";") : [],
          avatar: val.hasOwnProperty("Avatar") ? val.Avatar : "",
          status: val.hasOwnProperty(currentDay) ? val[currentDay] : "",
          recordId: item.id,
          class: kidClass,
        };

        return kid;
      });

      console.log("GetKidList");
      console.log(kidList);

      // remove first item and return
      return kidList;
    } catch (err) {
      throw err;
    }
  };

  return getKidList;
};

export const getClassListFactory = (optionConfig?: ApiConfig) => {
  const config = {
    ...AIRTABLE_API_CONFIG,
    ...optionConfig,
  };
  const instance = axios.create(config);

  const getClassList = async () => {
    try {
      console.log("getKidList");
      const requestUrl = `/${AIRTABLE_BASE_KEY}/Class?api_key=${AIRTABLE_API_KEY}&view=Classes`;
      console.log(requestUrl);
      const response = await instance.get(requestUrl);

      if (response.status !== 200) {
        throw new Error('Server Error');
      }

      console.log(response);

      const classList: string[] = response.data.records.map((item: any) => item.fields.ClassName);
      
      // remove first item and return
      return classList;
    } catch (err) {
      throw err;
    }
  };

  return getClassList;
};

export const updateAirTableFactory = (optionConfig?: ApiConfig) => {
  const config = {
    ...AIRTABLE_API_CONFIG,
    ...optionConfig,
  };
  const instance = axios.create(config);

  const updateAirTable = async (kidList: Kid[]) => {
    for (let i = 0; i < kidList.length; i++) {
      if (kidList[i].isSelected) {
        try {
          console.log("updateAirTable");
          const requestUrl = `/${AIRTABLE_BASE_KEY}/${kidList[i].class}/${kidList[i].recordId}/?api_key=${AIRTABLE_API_KEY}`;
          let currentDay = new Date().getDate();
          const requestBody = {
            fields: {
              currentDay: kidList[i].status
            }
          };
          console.log(requestUrl);
          const jsonStr = JSON.stringify(requestBody).replace("currentDay", currentDay.toString());
          console.log(JSON.stringify(requestBody).replace("currentDay", currentDay.toString()));
          const response = await instance.patch(requestUrl, JSON.parse(jsonStr));
          console.log(response);

        } catch (err) {
          console.log(err);
        }
      }

    }
  };

  return updateAirTable;
};

const CHATFULE_BOT_ID = "5d5c2aaffdaf79000144292a";
const CHATFUEL_TOKEN = "mELtlMAHYqR0BvgEiMq8zVek3uYUK3OJMbtyrdNPTrQB9ndV0fM7lWTFZbM4MZvD";
const CHATFUEL_MESSENGER = "Messenger";
const CHATFUEL_ZALO = "Zalo";
const CHATFUEL_BLOCK_DIHOC = "_DiHoc";
const CHATFUEL_BLOCK_NGHIHOC_COPHEP = "_NghiHoc_CoPhep";
const CHATFUEL_BLOCK_NGHIHOC = "_NghiHoc";
const CHATFUEL_BLOCK_VENHA = "_VeNha";
const CHATFUEL_DEFAULT_CONTACT_ID = "2612331648778972";
const CHATFUEL_API_CONFIG: ApiConfig = {
  baseURL: `https://api.chatfuel.com/bots/${CHATFULE_BOT_ID}/users`,
  timeout: 7000,
};
export const sendMessagesFactory = (optionConfig?: ApiConfig) => {
  const config = {
    ...CHATFUEL_API_CONFIG,
    ...optionConfig,
  };
  const instance = axios.create(config);

  const sendMessages = async (kid: Kid, messageType: string) => {
    try {
      let sendResult: boolean = false;

      let blockName;
      switch (messageType) {
        case MessageType.MESSAGE_GO_SCHOOL:
          blockName = CHATFUEL_BLOCK_DIHOC;
          break;

        case MessageType.MESSAGE_LEAVE_SCHOOL:
          blockName = CHATFUEL_BLOCK_VENHA;
          break;

        case MessageType.MESSAGE_ABSENT_ALLOW:
          blockName = CHATFUEL_BLOCK_NGHIHOC_COPHEP;
          break;

        case MessageType.MESSAGE_ABSENT:
          blockName = CHATFUEL_BLOCK_NGHIHOC;
          break;
      }

      const name = encodeURI(kid.name);
      for (let i = 0; i < kid.contactIds.length; i++) {
        let contactId = kid.contactIds ? kid.contactIds[i].trim() : "";
        let sendService = "";
        let postRequest = "";
        let sendBlock = "";
        if (contactId.startsWith("m/")) {
          sendService = CHATFUEL_MESSENGER;

          // remove prefix
          contactId = contactId.replace("m/", "");
          sendBlock = sendService + blockName;
          postRequest = `/${contactId}/send?chatfuel_token=${CHATFUEL_TOKEN}&chatfuel_message_tag=NON_PROMOTIONAL_SUBSCRIPTION&chatfuel_block_name=${sendBlock}&name=${name}`;
        } else if (contactId.startsWith("m/")) {
          sendService = CHATFUEL_ZALO;
          let zaloId = contactId.replace("z/", ""); // remove prefix
          postRequest = `/${CHATFUEL_DEFAULT_CONTACT_ID}/send?chatfuel_token=${CHATFUEL_TOKEN}&chatfuel_message_tag=NON_PROMOTIONAL_SUBSCRIPTION&chatfuel_block_name=${sendBlock}&name=${name}&zaloId=${zaloId}`;
        }

        console.log("sendMessages ");
        console.log(postRequest);
        const response = await instance.post(postRequest);
        if (response.status !== 200) {
          sendResult = sendResult || false;
          // throw new Error('Server Error');
        }

        sendResult = true;
      }

      return sendResult;
    } catch (err) {
      throw err;
    }
  };

  return sendMessages;
};
